import React from "react";

import Grid from "@mui/material/Grid";

import ServicePlanList from "./plans/ServicePlanList";
import TermsPolicySetup from "./terms-and-policy/TermsPolicySetup";

const ServicePlansPage = () => {
  return (
    <>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <TermsPolicySetup />
        </Grid>
        <Grid item xs={12}>
          <ServicePlanList />
        </Grid>
      </Grid>
    </>
  );
};

export default ServicePlansPage;
