import React, { useEffect, useState } from "react";

import Button from "@mui/material/Button";
import LinearProgress from "@mui/material/LinearProgress";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import { isEmpty } from "lodash";
import { useDispatch, useSelector } from "react-redux";

import { getRefundPolicy } from "../../../../store/slice/dashboard/servicePlanSlice";
import translate from "../../../hooks/translate";
import TermsPolicyForm from "./TermsPolicyForm";

const TermsPolicySetup = () => {
  const [dialogOpen, setDialogOpen] = useState(false);
  const dispatch = useDispatch();
  const loading = useSelector((state) => state.servicePlan.refundPolicyLoading);
  const refundPolicy = useSelector((state) => state.servicePlan.refundPolicy);

  const handleOpen = () => {
    setDialogOpen(true);
  };
  const handleClose = () => {
    setDialogOpen(false);
  };

  useEffect(() => {
    dispatch(getRefundPolicy());
  }, [dispatch]);
  return (
    <>
      <Paper variant="outlined" className="py-2 px-3">
        {loading ? (
          <LinearProgress />
        ) : (
          <Stack
            direction={{ xs: "column", sm: "row" }}
            spacing={1}
            alignItems="center"
            justifyContent="space-between"
          >
            <Typography variant="h6">
              {translate("consultant_setup_terms_and_policy")}
            </Typography>

            <Button variant="outlined" onClick={() => handleOpen()}>
              {isEmpty(refundPolicy) ? translate("consultant_setup") : "Edit"}
            </Button>

            <TermsPolicyForm
              open={dialogOpen}
              onClose={handleClose}
              type={isEmpty(refundPolicy) ? "add" : "edit"}
            />
          </Stack>
        )}
      </Paper>
    </>
  );
};

export default TermsPolicySetup;
