import React, { useEffect } from "react";

import KeyboardArrowDownSharpIcon from "@mui/icons-material/KeyboardArrowDownSharp";
import CloseIcon from "@mui/icons-material/Close";
import Autocomplete from "@mui/material/Autocomplete";
import Button from "@mui/material/Button";
import Checkbox from "@mui/material/Checkbox";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import FormControl from "@mui/material/FormControl";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormGroup from "@mui/material/FormGroup";
import FormHelperText from "@mui/material/FormHelperText";
import FormLabel from "@mui/material/FormLabel";
import Grid from "@mui/material/Grid";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import RadioGroup from "@mui/material/RadioGroup";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";

import { Formik } from "formik";
import { get, isEmpty, map } from "lodash";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import * as Yup from "yup";

import { getCurrencyList } from "../../../../store/slice/common";
import {
  getServicePlan,
  postServicePlanAdd,
} from "../../../../store/slice/dashboard/servicePlanSlice";
import translate from "../../../hooks/translate";
import CMDialog from "../../../ui-components/dialog/CMDialog";
import RichTextEditor from "../../../ui-components/editor/RichTextEditor";
import CMRadio from "../../../ui-components/form-components/CMRadio";
import { Box } from "@mui/material";
import { Drawer } from "@mui/material";
import "./RightModal.css"
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";


const consultantSessionsList = [
  { label: translate("common_weekly") },
  { label: translate("common_biweekly") },
  { label: translate("common_monthly") },
];

const ServicePlanAdd = (props) => {
  const { open, onClose, dialogData } = props;
  const dispatch = useDispatch();
  const currencyList = useSelector((state) => state.common.currencyList);
  const serviceTypes = useSelector((state) => state.servicePlan.serviceTypes);

  const serviceTypeOnline = get(serviceTypes, "serviceTypeOnline", false);
  const serviceTypeOffline = get(serviceTypes, "serviceTypeOffline", false);
  const serviceTypePhone = get(serviceTypes, "serviceTypePhone", false);

  const allServiceType =
    serviceTypeOnline || serviceTypeOffline || serviceTypePhone;
  useEffect(() => {
    dispatch(getCurrencyList());
  }, [dispatch]);

  const schemaObj = Yup.object().shape({
    planName: Yup.string()
      .min(5, translate("consultant_error_service_plan_name_too_short"))
      .max(50, translate("consultant_error_service_plan_name_too_long"))
      .required(translate("consultant_error_service_plan_name_required")),
    numberOfConsultant: Yup.number().required(
      translate("consultant_error_number_of_consultation_required")
    ),
    sessionName: Yup.string().when("numberOfConsultant", {
      is: (value) => value !== 1,
      then: Yup.string().required(
        translate("common_error_consultant_session_required")
      ),
      otherwise: Yup.string(),
    }),
    timeUnit: Yup.number().required(
      translate("common_error_time_duration_required")
    ),
    dietPlan: Yup.boolean().required(
      translate("common_error_diet_plan_required")
    ),
    currencyId: Yup.object().required(
      translate("consultant_error_currency_type_required")
    ),
    amount: Yup.number().required(
      translate("consultant_error_amount_required")
    ),
    details: Yup.string().required(
      translate("consultant_error_description_required")
    ),
  });


  // const schemaObj = Yup.object().shape({
  //   planName: Yup.string()
  //     .min(5, translate("consultant_error_service_plan_name_too_short"))
  //     .max(50, translate("consultant_error_service_plan_name_too_long"))
  //     .required(translate("consultant_error_service_plan_name_required")),
  //   numberOfConsultant: Yup.number().required(
  //     translate("consultant_error_number_of_consultation_required")
  //   ),
  //   sessionName: Yup.string().required(
  //     translate("common_error_consultant_session_required")
  //   ),
  //   timeUnit: Yup.number().required(
  //     translate("common_error_time_duration_required")
  //   ),
  //   dietPlan: Yup.boolean().required(
  //     translate("common_error_diet_plan_required")
  //   ),
  //   currencyId: Yup.object().required(
  //     translate("consultant_error_currency_type_required")
  //   ),
  //   amount: Yup.number().required(
  //     translate("consultant_error_amount_required")
  //   ),
  //   details: Yup.string().required(
  //     translate("consultant_error_description_required")
  //   ),
  // });

  // const handleChangeTab = (props,e) => {
  //   console.log(e.target.value, "93");
  //   props.handleChange
  // };

  const handleAddServicePlan = async (payload) => {
    payload.currencyId = get(payload, "currencyId._id", "");
    console.log(payload, "payload");

    if (get(dialogData, "formType") === "update") {
      payload._id = get(dialogData, "rowData._id");
    }
    const res = await dispatch(postServicePlanAdd(payload))
      .unwrap()
      .then((result) => {
        if (result.status) {
          dispatch(getServicePlan());
          onClose();
          toast.success(result.message);
        }
      })
      .catch((err) => {
        console.log(" handleAddServicePlan err", err);
      });
    return res;
  };

  return (
    <>
      <Drawer anchor="right" variant="temporary" open={open} onClose={onClose}>
        <div className="service-plan-add-container">
          <div className="service-plan-add-header">
            <Button onClick={onClose}>
              <ArrowBackIosIcon />
            </Button>
            <h2>{translate("consultant_add_service_plan")}</h2>
          </div>
          <Formik
            initialValues={{
              planName: get(dialogData, "rowData.planName", ""),
              numberOfConsultant: get(
                dialogData,
                "rowData.numberOfConsultant",
                ""
              ),
              sessionName: get(dialogData, "rowData.sessionName", ""),
              onlineConsultation: get(
                dialogData,
                "rowData.onlineConsultation",
                false
              ),
              offlineConsultation: get(
                dialogData,
                "rowData.offlineConsultation",
                false
              ),
              phoneConsultation: get(
                dialogData,
                "rowData.phoneConsultation",
                false
              ),
              currencyId: get(dialogData, "rowData.currencyId", ""),
              amount: get(dialogData, "rowData.amount", ""),
              timeUnit: get(dialogData, "rowData.timeUnit", 30),
              dietPlan: get(dialogData, "rowData.dietPlan", true),
              details: get(dialogData, "rowData.details", ""),
            }}
            validationSchema={schemaObj}
            onSubmit={(values) => {
              console.log("Values===", values);
              const checkAcceptType =
                values.onlineConsultation ||
                values.phoneConsultation ||
                values.offlineConsultation;

              if (allServiceType) {
                if (checkAcceptType) {
                  handleAddServicePlan(values);
                } else {
                  toast.error("Required select on service type", {
                    autoClose: false,
                  });
                }
              } else {
                toast.error(
                  "Please Setup Your Profile First Then Create Service Plan",
                  {
                    autoClose: false,
                  }
                );
              }
            }}
          >
            {(props) => (
              <form
                onSubmit={props.handleSubmit}
                className="d-flex flex-column overflow-auto"
              >
                {/* {console.log("props: ", props)} */}
                <DialogContent dividers>
                  <Grid container spacing={2}>
                    <Grid item xs={12} container>
                      <Grid item xs={12} sm={9.9} md={7.9}>
                        <TextField
                          label={translate("consultant_service_plan_name")}
                          variant="outlined"
                          name="planName"
                          value={props.values.planName}
                          onChange={props.handleChange}
                          onBlur={props.handleBlur}
                          error={
                            props.errors.planName && props.touched.planName
                              ? true
                              : false
                          }
                          helperText={
                            props.errors.planName && props.touched.planName
                              ? props.errors.planName
                              : null
                          }
                        />
                      </Grid>
                    </Grid>

                    <Grid item xs={12} container spacing={2}>
                      <Grid item md={3} sm={4} xs={12}>
                        <TextField
                          label={translate("consultant_number_of_consultation")}
                          variant="outlined"
                          name="numberOfConsultant"
                          value={props.values.numberOfConsultant}
                          onChange={props.handleChange}
                          onBlur={props.handleBlur}
                          error={
                            props.errors.numberOfConsultant &&
                            props.touched.numberOfConsultant
                              ? true
                              : false
                          }
                          helperText={
                            props.errors.numberOfConsultant &&
                            props.touched.numberOfConsultant
                              ? props.errors.numberOfConsultant
                              : null
                          }
                        />
                      </Grid>
                      {console.log(props.values.numberOfConsultant, "233")}

                      {/* <Grid item md={4} sm={6} xs={12} className="disabled">
                        <FormControl fullWidth>
                          <InputLabel id="demo-simple-select-label">
                            {translate("consultant_consultant_sessions")}
                          </InputLabel>
                          <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            name="sessionName"
                            value={props.values.sessionName}
                            label={translate("consultant_consultant_sessions")}
                            onChange={props.handleChange}
                          >
                            {map(consultantSessionsList, (item, index) => (
                              <MenuItem value={item.label} key={index}>
                                {item.label}
                              </MenuItem>
                            ))}
                          </Select>
                          {props.errors.sessionName &&
                          props.touched.sessionName ? (
                            <FormHelperText error>
                              {props.errors.sessionName}
                            </FormHelperText>
                          ) : null}
                        </FormControl>
                      </Grid> */}

                      {/* <Grid item md={4} sm={6} xs={12}>
                        <FormControl fullWidth>
                          <InputLabel id="demo-simple-select-label">
                            {translate("consultant_consultant_sessions")}
                          </InputLabel>
                          <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            name="sessionName"
                            value={props.values.sessionName}
                            label={translate("consultant_consultant_sessions")}
                            onChange={props.handleChange}
                            disabled={
                              parseInt(props.values.numberOfConsultant, 10) ===
                              1
                            }
                          >
                            {map(consultantSessionsList, (item, index) => (
                              <MenuItem value={item.label} key={index}>
                                {item.label}
                              </MenuItem>
                            ))}
                          </Select>
                          {props.errors.sessionName &&
                          props.touched.sessionName ? (
                            <FormHelperText error>
                              {props.errors.sessionName}
                            </FormHelperText>
                          ) : null}
                        </FormControl>
                      </Grid> */}
                      <Grid item md={4} sm={6} xs={12}>
                        <FormControl fullWidth>
                          <InputLabel id="demo-simple-select-label">
                            {translate("consultant_consultant_sessions")}
                          </InputLabel>
                          <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            name="sessionName"
                            value={props.values.sessionName}
                            label={translate("consultant_consultant_sessions")}
                            onChange={props.handleChange}
                            disabled={
                              parseInt(props.values.numberOfConsultant, 10) ===
                              1
                            }
                          >
                            {map(consultantSessionsList, (item, index) => (
                              <MenuItem value={item.label} key={index}>
                                {item.label}
                              </MenuItem>
                            ))}
                          </Select>
                          {props.errors.sessionName &&
                          props.touched.sessionName ? (
                            <FormHelperText error>
                              {props.errors.sessionName}
                            </FormHelperText>
                          ) : null}
                        </FormControl>
                      </Grid>

                      <Grid item xs={12} md={5}>
                        <FormControl component="fieldset" variant="standard">
                          <FormLabel>
                            {translate("consultant_consultation_type")}
                          </FormLabel>
                          <FormGroup row>
                            <FormControlLabel
                              value="online"
                              control={
                                <Checkbox
                                  name="onlineConsultation"
                                  checked={props.values.onlineConsultation}
                                  onChange={props.handleChange}
                                />
                              }
                              disabled={!serviceTypeOnline}
                              label={translate("common_online")}
                            />
                            <FormControlLabel
                              control={
                                <Checkbox
                                  name="offlineConsultation"
                                  checked={props.values.offlineConsultation}
                                  onChange={props.handleChange}
                                />
                              }
                              disabled={!serviceTypeOffline}
                              label={translate("common_offline")}
                            />
                            <FormControlLabel
                              control={
                                <Checkbox
                                  name="phoneConsultation"
                                  checked={props.values.phoneConsultation}
                                  onChange={props.handleChange}
                                />
                              }
                              disabled={!serviceTypePhone}
                              label={translate("common_phone")}
                            />
                          </FormGroup>
                          {props.errors.onlineConsultation &&
                            props.touched.onlineConsultation && (
                              <FormHelperText error>
                                Type is Required
                              </FormHelperText>
                            )}
                        </FormControl>
                      </Grid>
                    </Grid>

                    <Grid item sm={4} md={3} xs={12}>
                      <Autocomplete
                        getOptionLabel={(option) => {
                          return !isEmpty(option) ? option.currency : "";
                        }}
                        options={currencyList}
                        name="currencyId"
                        value={props.values.currencyId}
                        isOptionEqualToValue={(option, value) => {
                          if (value === "" || option._id === value._id) {
                            return true;
                          }
                        }}
                        onChange={(e, value) => {
                          props.setFieldValue("currencyId", value);
                        }}
                        disablePortal
                        autoHighlight
                        disableClearable
                        noOptionsText={translate("common_no_data")}
                        popupIcon={<KeyboardArrowDownSharpIcon />}
                        renderOption={(props, option) => (
                          <Box
                            key={option._id}
                            component="li"
                            sx={{ "& > img": { mr: 1, flexShrink: 0 } }}
                            {...props}
                          >
                            <Box component="span" mr={1}>
                              {option.currency}
                            </Box>
                            {option.currencySymbol}
                          </Box>
                        )}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            label={translate("consultant_currency_type")}
                            name="currencyId"
                            error={
                              props.errors.currencyId &&
                              props.touched.currencyId
                                ? true
                                : false
                            }
                            helperText={
                              props.errors.currencyId &&
                              props.touched.currencyId
                                ? props.errors.currencyId
                                : null
                            }
                          />
                        )}
                      />
                    </Grid>

                    <Grid item sm={6} md={5} xs={12}>
                      <TextField
                        label={translate("consultant_amount")}
                        variant="outlined"
                        name="amount"
                        value={props.values.amount}
                        onChange={props.handleChange}
                        onBlur={props.handleBlur}
                        inputProps={{ type: "number" }}
                        error={
                          props.errors.amount && props.touched.amount
                            ? true
                            : false
                        }
                        helperText={
                          props.errors.amount && props.touched.amount
                            ? props.errors.amount
                            : null
                        }
                      />
                    </Grid>

                    <Grid item xs={12} sm={6}>
                      <FormControl fullWidth>
                        <FormLabel htmlFor="weight" className="mb-1">
                          {translate("consultant_time_duration")}
                        </FormLabel>

                        <RadioGroup
                          name="timeUnit"
                          value={props.values.timeUnit}
                          onChange={(e, value) => {
                            props.setFieldValue("timeUnit", value);
                          }}
                          row
                          sx={{ marginLeft: "10px", marginTop: "10px" }}
                        >
                          <FormControlLabel
                            value={30}
                            control={<CMRadio />}
                            label={
                              "30 " + translate("common_time_length_minute")
                            }
                          />
                          <FormControlLabel
                            value={45}
                            control={<CMRadio />}
                            label={
                              "45 " + translate("common_time_length_minute")
                            }
                          />
                          <FormControlLabel
                            value={60}
                            control={<CMRadio />}
                            label={"1 " + translate("common_time_length_hour")}
                          />
                        </RadioGroup>
                      </FormControl>
                    </Grid>

                    <Grid item xs={12} sm={6}>
                      <FormControl fullWidth>
                        <FormLabel htmlFor="weight" className="mb-1">
                          {translate("common_diet_plan")}
                        </FormLabel>
                      </FormControl>
                      <FormControl fullWidth>
                        <RadioGroup
                          name="dietPlan"
                          value={props.values.dietPlan}
                          onChange={(e, value) => {
                            props.setFieldValue("dietPlan", value);
                          }}
                          row
                          sx={{ marginLeft: "10px", marginTop: "10px" }}
                        >
                          <FormControlLabel
                            value={true}
                            control={<CMRadio />}
                            label={translate("common_type_yes")}
                          />
                          <FormControlLabel
                            value={false}
                            control={<CMRadio />}
                            label={translate("common_type_no")}
                          />
                        </RadioGroup>
                      </FormControl>
                    </Grid>

                    <Grid item xs={12}>
                      <RichTextEditor
                        name="details"
                        value={props.values.details}
                        setFieldValue={props.setFieldValue}
                        error={
                          props.errors.details && props.touched.details
                            ? true
                            : false
                        }
                        helperText={
                          props.errors.details && props.touched.details
                            ? props.errors.details
                            : null
                        }
                      />
                    </Grid>
                  </Grid>
                </DialogContent>
                <DialogActions>
                  <Button variant="contained" type="submit">
                    {translate("common_add")}
                  </Button>
                </DialogActions>
              </form>
            )}
          </Formik>
        </div>
      </Drawer>
    </>
  );
};

export default ServicePlanAdd;
