import React from "react";
import PercentIcon from "@mui/icons-material/Percent";
import Button from "@mui/material/Button";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import FormControl from "@mui/material/FormControl";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormLabel from "@mui/material/FormLabel";
import Grid from "@mui/material/Grid";
import InputAdornment from "@mui/material/InputAdornment";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";
import { Formik } from "formik";
import { get } from "lodash";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import * as Yup from "yup";
import { Drawer } from "@mui/material";
import {
  getRefundPolicy,
  postRefundPolicyAdd,
} from "../../../../store/slice/dashboard/servicePlanSlice";
import translate from "../../../hooks/translate";
import RichTextEditor from "../../../ui-components/editor/RichTextEditor";
import KeyboardArrowDownSharpIcon from "@mui/icons-material/KeyboardArrowDownSharp";
import Autocomplete from "@mui/material/Autocomplete";

import useContextData from "../../../hooks/useContextData";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";


const TermsPolicyForm = (props) => {
  const { open, onClose, type } = props;
  const dispatch = useDispatch();
  const refundPolicy = useSelector((state) => state.servicePlan.refundPolicy);
  const { GetLanguageList } = useContextData();
  const LanguageList = GetLanguageList();

  const schemaObj = Yup.object().shape({
    content: Yup.string().required(
      translate("consultant_error_terms_and_policy_required")
    ),
    language: Yup.object()
      .shape()
      .required(translate("consultant_error_languages_required"))
      .nullable(),
    refundMethod: Yup.string().required(translate("common_error_required")),
    percentage: Yup.number().when("refundMethod", {
      is: (value) => value === "PRO_RATE_CANCEL_CHARGE",
      then: Yup.number()
        .min(0, translate("consultant_error_minimum_value_0"))
        .max(100, translate("consultant_error_maximum_value_100"))
        .positive(translate("consultant_error_enter_positive_number"))
        .required(translate("consultant_error_percentage_required")),
      otherwise: Yup.number().notRequired(),
    }),
  });

  const handleTermsCondition = (data) => {
    console.log(data?.language?._id, "63");
    const payload =
      type === "edit"
        ? {
            ...data,
            _id: refundPolicy._id,
            language: data?.language?._id,
          }
        : { ...data, language: data?.language?._id };
    dispatch(postRefundPolicyAdd(payload))
      .unwrap()
      .then((result) => {
        if (get(result, "status", false)) {
          toast.success(result.message);
          dispatch(getRefundPolicy());
          onClose();
        } else {
          toast.error(result.message);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <>
      <Drawer
        anchor="right"
        open={open}
        onClose={onClose}
        className="terms-details-design"
      >
        <div className="back-icon-common">
          <Button onClick={onClose}>
            <ArrowBackIosIcon />
          </Button>
        </div>
        <Formik
          initialValues={{
            content: get(refundPolicy, "content", ""),
            refundMethod: get(refundPolicy, "refundMethod", "PRO_RATE"),
            percentage: get(refundPolicy, "percentage", 0),
            language: get(refundPolicy, "language", ""),
          }}
          validationSchema={schemaObj}
          onSubmit={(values) => {
            console.log("Values===", values);
            handleTermsCondition(values);
          }}
        >
          {(props) => (
            <form
              onSubmit={props.handleSubmit}
              className="d-flex flex-column overflow-auto"
            >
              <DialogContent dividers>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <Typography variant="h5" className="mb-2">
                      {translate("consultant_terms_and_policy")}
                    </Typography>
                    <RichTextEditor
                      name="content"
                      value={props.values.content}
                      setFieldValue={props.setFieldValue}
                      error={
                        props.errors.content && props.touched.content
                          ? true
                          : false
                      }
                      helperText={
                        props.errors.content && props.touched.content
                          ? props.errors.content
                          : null
                      }
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl fullWidth>
                      <FormLabel htmlFor="language" className="mb-2">
                        {translate("common_language")}
                      </FormLabel>

                      <Autocomplete
                        getOptionLabel={(option) => {
                          return option.title ?? "";
                        }}
                        options={LanguageList}
                        name="language"
                        value={
                          typeof props.values.language === "string"
                            ? LanguageList.find(
                                (option) => option._id === props.values.language
                              )
                            : props.values.language
                        }
                        isOptionEqualToValue={(option, value) => {
                          if (value === "" || option._id === value._id) {
                            return true;
                          }
                        }}
                        onChange={(_e, value) => {
                          props.setFieldValue("language", value);
                        }}
                        onBlur={props.handleBlur}
                        disablePortal
                        autoHighlight
                        disableClearable
                        noOptionsText={translate("common_no_data")}
                        popupIcon={<KeyboardArrowDownSharpIcon />}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            placeholder={translate(
                              "consultant_select_language"
                            )}
                            name="language"
                            error={
                              props.errors.language && props.touched.language
                                ? true
                                : false
                            }
                            helperText={
                              props.errors.language && props.touched.language
                                ? props.errors.language
                                : null
                            }
                          />
                        )}
                      />
                    </FormControl>
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <FormControl fullWidth>
                      <FormLabel className="mb-1">
                        {translate("consultant_refund_method")}
                      </FormLabel>
                      <RadioGroup
                        name="refundMethod"
                        value={props.values.refundMethod}
                        onChange={(e, value) => {
                          props.setFieldValue("refundMethod", value);
                          props.setFieldValue("percentage", 0);
                        }}
                        row
                      >
                        <FormControlLabel
                          value="PRO_RATE"
                          control={<Radio />}
                          label={translate("consultant_only_pro_rate")}
                        />
                        <FormControlLabel
                          value="PRO_RATE_CANCEL_CHARGE"
                          control={<Radio />}
                          label={translate(
                            "consultant_pro_rate_and_cancellation_charges"
                          )}
                        />
                      </RadioGroup>
                    </FormControl>
                  </Grid>

                  {props.values.refundMethod === "PRO_RATE_CANCEL_CHARGE" && (
                    <Grid item md={4} sm={6} xs={12}>
                      <Typography variant="body1" className="mb-2">
                        {translate("consultant_enter_percentage")}
                      </Typography>
                      <TextField
                        placeholder="percentage"
                        variant="outlined"
                        name="percentage"
                        value={props.values.percentage}
                        onChange={props.handleChange}
                        onBlur={props.handleBlur}
                        inputProps={{ type: "number" }}
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <PercentIcon />
                            </InputAdornment>
                          ),
                        }}
                        error={
                          props.errors.percentage && props.touched.percentage
                            ? true
                            : false
                        }
                        helperText={
                          props.errors.percentage && props.touched.percentage
                            ? props.errors.percentage
                            : null
                        }
                      />
                    </Grid>
                  )}
                </Grid>
              </DialogContent>
              <DialogActions>
                <Button variant="contained" type="submit">
                  {translate("common_btn_submit")}
                </Button>
              </DialogActions>
            </form>
          )}
        </Formik>
      </Drawer>
    </>
  );
};

export default TermsPolicyForm;
