import React from "react";

import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";

import PageHeader from "../../components/common/PageHeader";
import ServicePlansPage from "../../components/dashboard/service-plans";
import Page from "../../components/common/Page";

const ServiceAndPlans = () => {
  return (
    <>
      <Page title="Service Plan | InspireMind">
        <Box component="section" className="w-100">
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <PageHeader
                title="consultant_service_plan"
                variant="outlined"
                elevation={0}
              />
            </Grid>
            <Grid item xs={12}>
              <Box>
                <ServicePlansPage />
              </Box>
            </Grid>
          </Grid>
        </Box>
      </Page>
    </>
  );
};

export default ServiceAndPlans;
