import React, { useCallback, useEffect, useMemo, useState } from "react";

import ArrowRightAltIcon from "@mui/icons-material/ArrowRightAlt";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import CardContent from "@mui/material/CardContent";
import IconButton from "@mui/material/IconButton";
import LinearProgress from "@mui/material/LinearProgress";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Switch from "@mui/material/Switch";
import Typography from "@mui/material/Typography";

import { get, isEmpty } from "lodash";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import {
  deleteServicePlan,
  getServicePlan,
  getServiceTypes,
  updateServicePlanStatus,
} from "../../../../store/slice/dashboard/servicePlanSlice";
import Label from "../../../common/Label";
import CMDataGrid from "../../../ui-components/data-grid/CMDataGrid";
import CMDataGridToolbar from "../../../ui-components/data-grid/CMDataGridToolbar";
import EmptyTableRowOverlay from "../../../ui-components/data-grid/EmptyTableRowOverlay";
import ServicePlanAdd from "./ServicePlanAdd";
import ServiceplanDetail from "./ServiceplanDetail";
import translate from "../../../hooks/translate";

export default function ServicePlanList() {
  const [open, setOpen] = useState(false);
  const [viewOpen, setViewOpen] = useState(false);
  const [rowId, setRowId] = useState("");
  const [dialogData, setDialogData] = useState([]);
  const dispatch = useDispatch();
  const servicePlanData = useSelector((state) => state.servicePlan.details);
  const serviceTypes = useSelector((state) => state.servicePlan.serviceTypes);
  const isLoading = useSelector((state) => state.servicePlan.loading);

  const navigate = useNavigate();

  const handleDialogOpen = useCallback(() => {
    setOpen(true);
  }, []);

  const handleDialogClose = useCallback(() => {
    setOpen(false);
  }, []);

  const handleViewDialogOpen = useCallback(() => {
    setViewOpen(true);
  }, []);

  const handleViewDialogClose = useCallback(() => {
    setViewOpen(false);
  }, []);

  const serviceTypeOnline = get(serviceTypes, "serviceTypeOnline", false);
  const serviceTypeOffline = get(serviceTypes, "serviceTypeOffline", false);
  const serviceTypePhone = get(serviceTypes, "serviceTypePhone", false);

  const handleDelete = useCallback(
    (id) => {
      dispatch(deleteServicePlan(id))
        .unwrap()
        .then((result) => {
          if (result.status) {
            dispatch(getServicePlan());
            toast.success(result.message);
          }
        })
        .catch((err) => {
          console.log(" handleAddServicePlan err", err);
        });
    },
    [dispatch]
  );

  const handleStatus = useCallback(
    async (event, id) => {
      setRowId(id);
      const payload = {
        _id: id,
        status: event.target.checked,
      };
      const res = await dispatch(updateServicePlanStatus(payload))
        .unwrap()
        .then((result) => {
          if (result.status) {
            dispatch(getServicePlan());
            toast.success(result.message);
          }
        })
        .catch((error) => {
          console.log("Service plan update error=======>", error);
        });
      return res;
    },
    [dispatch]
  );

  useEffect(() => {
    dispatch(getServicePlan());
    dispatch(getServiceTypes());
  }, [dispatch]);

  const tableColumns = useMemo(
    () => [
      {
        field: "planName",
        headerName: translate("consultant_service_plan_name"),
        width: 160,
      },
      {
        field: "numberOfConsultant",
        headerName: translate("consultant_number_of_consultation"),
        width: 130,
        hide: true, // Set hide to true for the column
      },
      {
        field: "sessionName",
        headerName: translate("consultant_consultant_sessions"),
        width: 130,
        renderCell: (params) => (
          <Box>
            <Label color="secondary">
              {get(params, "row.sessionName", "")}
            </Label>
          </Box>
        ),
      },
      {
        field: "currency",
        headerName: translate("consultant_currency_type"),
        width: 130,
        hide: true, // Set hide to true for the column
        renderCell: (params) => (
          <Typography
            variant="body2"
            sx={{ display: "flex", alignItems: "center" }}
          >
            {get(params, "row.currencyId.currency", "")}
            <Typography variant="body1" component="span" ml={0.5}>
              {get(params, "row.currencyId.currencySymbol", "")}
            </Typography>
          </Typography>
        ),
      },
      {
        field: "onlineConsultation,offlineConsultation,phoneConsultation",
        headerName: translate("consultant_consultation_type"),
        width: 130,
        hide: true, // Set hide to true for the column
        renderCell: (params) => (
          <Stack direction="column" spacing={1}>
            <Stack direction="row" spacing={1}>
              {get(params, "row.onlineConsultation", false) && (
                <Label color="secondary">Online</Label>
              )}
              {get(params, "row.offlineConsultation", false) && (
                <Label color="secondary">Offline</Label>
              )}
            </Stack>
            {get(params, "row.phoneConsultation", false) && (
              <Label color="secondary">Phone</Label>
            )}
          </Stack>
        ),
      },
      {
        field: "timeUnit",
        headerName: translate("consultant_time_duration"),
        width: 130,
        renderCell: (params) => (
          <Typography variant="subtitle2">
            {get(params, "row.timeUnit", "")} Min
          </Typography>
        ),
      },
      {
        field: "dietPlan",
        headerName: translate("common_diet_plan"),
        width: 130,
        renderCell: (params) => (
          <Label color="secondary" variant="outlined">
            {get(params, "row.dietPlan", false) ? "Yes" : "No"}
          </Label>
        ),
      },
      {
        field: "amount,currencyId",
        headerName: translate("common_service_charges"),
        width: 130,
        renderCell: (params) => (
          <Typography
            variant="body2"
            sx={{ display: "flex", alignItems: "center" }}
          >
            {get(params, "row.amount", "")}
            <Typography variant="body1" component="span" ml={0.5}>
              {get(params, "row.currencyId.currencySymbol", "")}
            </Typography>
          </Typography>
        ),
      },
      {
        field: "view",
        headerName: translate("common_view"),
        headerAlign: "center",
        width: 130,
        renderCell: (params) => (
          <IconButton
            onClick={() => {
              setDialogData({
                ...dialogData,
                rowData: params.row,
                formType: "show",
              });
              handleViewDialogOpen();
            }}
            color="primary"
            size="large"
          >
            <RemoveRedEyeIcon />
          </IconButton>
        ),
      },
      {
        field: "Actions",
        headerName: translate("common_actions"),
        headerAlign: "center",
        width: 140,
        renderCell: (params) => {
          return (
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <IconButton
                sx={{ color: "success.dark" }}
                onClick={() => {
                  setDialogData({
                    ...dialogData,
                    rowData: params.row,
                    formType: "update",
                  });
                  handleDialogOpen();
                }}
              >
                <EditIcon />
              </IconButton>
              <Switch
                checked={get(params, "row.status", true)}
                onChange={(e) => handleStatus(e, params.row._id)}
                disabled={rowId === params.row._id ? isLoading : false}
                inputProps={{
                  "aria-label": `plan-status`,
                  role: "switch",
                }}
              />
              <IconButton
                color="error"
                onClick={() => handleDelete(params.row._id)}
              >
                <DeleteIcon />
              </IconButton>
            </Box>
          );
        },
      },
    ],
    [
      dialogData,
      handleDelete,
      handleDialogOpen,
      handleStatus,
      handleViewDialogOpen,
      isLoading,
      rowId,
    ]
  );

  const CustomToolbar = () => {
    return (
      <CMDataGridToolbar align="right" divider={true}>
        {serviceTypeOnline ||
        serviceTypeOffline ||
        serviceTypePhone ? //     setDialogData({ //   onClick={() => { //   variant="contained" // <Button
        //       ...dialogData,
        //       rowData: [],
        //       formType: "create",
        //     });
        //     handleDialogOpen();
        //   }}
        // >
        //   {translate("consultant_add_service_plan")}
        // </Button>
        null : (
          <Button
            endIcon={<ArrowRightAltIcon />}
            onClick={() => {
              navigate("/dashboard/appointment/service-type");
            }}
          >
            {translate("consultant_setup")}
          </Button>
        )}
      </CMDataGridToolbar>
    );
  };

  return (
    <>
      <Paper variant="outlined" className="h-100 service-plan-design">
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            alignItems: "center",
            marginTop: "20px",
            marginRight: "20px",
            marginBottom: "0px",
          }}
        >
          {serviceTypeOnline || serviceTypeOffline || serviceTypePhone ? (
            <Button
              variant="contained"
              onClick={() => {
                setDialogData({
                  ...dialogData,
                  rowData: [],
                  formType: "create",
                });
                handleDialogOpen();
              }}
            >
              {translate("consultant_add_service_plan")}
            </Button>
          ) : (
            <Button
              endIcon={<ArrowRightAltIcon />}
              onClick={() => {
                navigate("/dashboard/appointment/service-type");
              }}
            >
              {translate("consultant_setup")}
            </Button>
          )}
        </div>
        <CardContent>
          <CMDataGrid
            rows={!isEmpty(servicePlanData) ? servicePlanData : []}
            columns={tableColumns}
            getRowId={(row) => row._id}
            loading={isLoading}
            components={{
              Toolbar: CustomToolbar,
              NoRowsOverlay: EmptyTableRowOverlay,
              LoadingOverlay: LinearProgress,
            }}
            className="min-vh-100"
          />
        </CardContent>
      </Paper>

      <ServicePlanAdd
        open={open}
        onClose={handleDialogClose}
        dialogData={dialogData}
      />
      <ServiceplanDetail
        open={viewOpen}
        onClose={handleViewDialogClose}
        dialogData={dialogData}
      />
    </>
  );
}
